.profile__user-name {
  max-width: 294px;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin: 0;
}

@media screen and (max-width: 400px) {
  .profile__user-name {
    max-width: 189px;
    font-size: 27px;
    line-height: 36px;
  }
}
