.header__logo {
  max-width: 142px;
  max-height: 33px;
  object-fit: cover;
  object-position: right top;
  display: block;
}

@media screen and (max-width: 900px) {
  .header__logo {
    padding-left: 27px;
  }
}

@media screen and (max-width: 320px) {
  .header__logo {
    max-width: 103.74px;
    max-height: 24.4px;
  }
}
